"use client"

import { useState, useEffect, useCallback, useRef } from "react"
import type { User } from "@supabase/supabase-js"
import { ResizableHandle, ResizablePanel, ResizablePanelGroup } from "@/components/ui/resizable"
import { FileExplorer } from "./file-explorer"
import { SplitEditor } from "./split-editor"
import { Terminal } from "./terminal"
import { Button } from "@/components/ui/button"
import { RefreshCw, Key } from "lucide-react"
import { CommandParser } from "@/lib/terminal/command-parser"
import { CommandValidator } from "@/lib/terminal/command-validator"
import { TerminalExecutor } from "@/lib/terminal/terminal-executor"
import { windowsCommands } from "@/lib/terminal/windows-commands"
import { AdvancedDebugPanel } from "./advanced-debug-panel"
import { AIAssistantPanel } from "./ai-assistant-panel"
import { EnhancedCollaboratorsPanel } from "./collaborators-panel"
import { IDEHeader } from "./ide-header"
import { FindReplace, type FindOptions } from "./find-replace"
import { Breadcrumb } from "./breadcrumb"
import { ExecutionOutput } from "./execution-output"
import { getExecutionManager } from "@/lib/execution/execution-manager"
import { detectLanguage } from "@/lib/execution/types"
import type { ExecutionResult, ExecutionStatus } from "@/lib/execution/types"
import { WorkspaceManager } from "@/lib/workspace-manager"
import { FileWatcher, type FileChange } from "@/lib/file-watcher"
import { DragDropHandler, type DroppedFile, type OpenFile } from "@/lib/drag-drop"
import { LanguageManager } from "@/lib/languages/language-manager"
import { DebugAdapter } from "@/lib/debug/debug-adapter"
import { toast } from "sonner"

export interface FileNode {
  id: string
  name: string
  type: "file" | "folder"
  content?: string
  language?: string
  children?: FileNode[]
}

export type { OpenFile }

interface IDEWorkspaceProps {
  projectId: string
  user: User & { roomId?: string; roomPassword?: string }
  onRoomSettingsChange?: (settings: { roomId: string; roomPassword: string }) => void
  connectionError?: string | null
  isConnecting?: boolean
  onConnect?: () => void
}

import { CollaborationProvider, useCollaboration } from '@/lib/collaboration/collaboration-context';

export function IDEWorkspace({ projectId, user }: IDEWorkspaceProps) {
  const [roomId, setRoomId] = useState(projectId);
  const [roomPassword, setRoomPassword] = useState('');
  const [isConnecting, setIsConnecting] = useState(false);
  const [connectionError, setConnectionError] = useState<string | null>(null);

  const handleRoomSettingsChange = useCallback((settings: { roomId: string; roomPassword: string }) => {
    setRoomId(settings.roomId);
    setRoomPassword(settings.roomPassword);
    setConnectionError(null);
  }, []);

  const handleAuthError = useCallback((error: string) => {
    setConnectionError(error);
    setIsConnecting(false);
  }, []);

  return (
    <CollaborationProvider 
      roomId={roomId}
      roomPassword={roomPassword}
      initialCode={''}
      user={user}
      onAuthError={handleAuthError}
    >
      <WorkspaceWithCollab 
        projectId={roomId} 
        user={user} 
        onRoomSettingsChange={handleRoomSettingsChange}
        connectionError={connectionError}
        isConnecting={isConnecting}
        onConnect={() => setIsConnecting(true)}
      />
    </CollaborationProvider>
  );
}

// WorkspaceWithCollab implementation below is unique and not duplicated.
function WorkspaceWithCollab({ 
  projectId, 
  user, 
  onRoomSettingsChange, 
  connectionError,
  isConnecting,
  onConnect 
}: IDEWorkspaceProps) {
  // File state
  const [files, setFiles] = useState<FileNode[]>([])
  const [openFiles, setOpenFiles] = useState<OpenFile[]>([])
  const [activeFileId, setActiveFileId] = useState<string | null>(null)
  const [workspaceName, setWorkspaceName] = useState<string | null>(null)

  // UI state
  const [showTerminal, setShowTerminal] = useState(true)
  const [showDebugPanel, setShowDebugPanel] = useState(false)
  const [showCollaborators, setShowCollaborators] = useState(false)
  const [showAIPanel, setShowAIPanel] = useState(false)
  const [showFindReplace, setShowFindReplace] = useState(false)
  const [showExecutionOutput, setShowExecutionOutput] = useState(false)
  const [fontSize, setFontSize] = useState(14)

  // Execution state
  const [executionStatus, setExecutionStatus] = useState<ExecutionStatus>('idle')
  const [executionResult, setExecutionResult] = useState<ExecutionResult | null>(null)
  const executionManagerRef = useRef(getExecutionManager())
  const parserRef = useRef<CommandParser>(new CommandParser())
  const validatorRef = useRef<CommandValidator>(new CommandValidator())
  const executorRef = useRef<TerminalExecutor | null>(null)
  const envRef = useRef<Map<string, string>>(new Map())
  const filesRef = useRef<FileNode[]>(files)

  // Debug state
  const [breakpoints, setBreakpoints] = useState<Map<string, Set<number>>>(new Map())
  const [isDebugging, setIsDebugging] = useState(false)
  const [isDebugPaused, setIsDebugPaused] = useState(false)
  const [activeLine, setActiveLine] = useState<number | null>(null)

  // Managers
  const workspaceManagerRef = useRef<WorkspaceManager>(new WorkspaceManager())
  const fileWatcherRef = useRef<FileWatcher>(new FileWatcher())
  const dragDropHandlerRef = useRef<DragDropHandler | null>(null)
  const languageManagerRef = useRef<LanguageManager>(new LanguageManager())
  const debugAdapterRef = useRef<DebugAdapter>(new DebugAdapter())
  const workspaceRef = useRef<HTMLDivElement>(null)

  // Initialize drag and drop
  useEffect(() => {
    if (workspaceRef.current && !dragDropHandlerRef.current) {
      const handler = new DragDropHandler(workspaceRef.current)

      handler.onFilesDropped((droppedFiles) => {
        handleDroppedFiles(droppedFiles)
      })

      dragDropHandlerRef.current = handler
    }

    return () => {
      dragDropHandlerRef.current?.destroy()
    }
  }, [])

  // Subscribe to debug updates
  useEffect(() => {
    debugAdapterRef.current.setUpdateCallback((state) => {
      setActiveLine(state.currentLine)
      setIsDebugPaused(state.session?.state === 'paused')
      setIsDebugging(state.session !== null)
    })
  }, [])

  // Keep refs in sync
  useEffect(() => {
    filesRef.current = files
  }, [files])

  // Initialize terminal executor
  useEffect(() => {
    // Use dynamic access to terminal instance since it's set later
    const terminalIO = {
      write: (text: string) => (window as any).__terminal?.write?.(text),
      writeln: (text: string) => (window as any).__terminal?.writeln?.(text),
    }

    executorRef.current = new TerminalExecutor({
      workingDirectory: "/workspace",
      environment: envRef.current,
      user: user?.email || "anonymous",
      terminal: terminalIO,
      onRunCode: async (filename: string) => {
        const findFile = (nodes: FileNode[]): FileNode | null => {
          for (const node of nodes) {
            if (node.name === filename) return node
            if (node.children) {
              const found = findFile(node.children)
              if (found) return found
            }
          }
          return null
        }

        const file = findFile(filesRef.current)
        if (!file) {
          terminalIO.writeln(`\x1b[31mError: File ${filename} not found\x1b[0m`)
          return
        }

        if (file.type !== 'file') {
          terminalIO.writeln(`\x1b[31mError: ${filename} is not a file\x1b[0m`)
          return
        }

        terminalIO.writeln(`\x1b[90mRunning ${file.name}...\x1b[0m`)

        try {
          const language = LanguageManager.getLanguageFromFile(file.name)
          const result = await languageManagerRef.current.execute(
            language,
            file.content || "",
            file.name,
            (line) => terminalIO.writeln(line)
          )

          if (result.error) {
            terminalIO.writeln(`\x1b[31mError: ${result.error}\x1b[0m`)
          }
        } catch (e) {
          terminalIO.writeln(`\x1b[31mExecution failed: ${e}\x1b[0m`)
        }
      }
    })

    Object.entries(windowsCommands).forEach(([cmd, handler]) => {
      executorRef.current!.registerCommand(cmd, handler)
    })
  }, [user])

  // Handle dropped files
  const handleDroppedFiles = async (droppedFiles: DroppedFile[]) => {
    for (const file of droppedFiles) {
      const newFile: OpenFile = {
        id: `${Date.now()}-${file.name}`,
        name: file.name,
        content: file.content,
        language: getLanguageFromFilename(file.name),
        isDirty: false,
      }

      setOpenFiles(prev => [...prev, newFile])
      setActiveFileId(newFile.id)
      toast.success(`Added ${file.name}`)
    }
  }

  // Open local folder
  const handleOpenFolder = async () => {
    try {
      const result = await workspaceManagerRef.current.openFolder()
      if (result) {
        setFiles(result.files)
        setWorkspaceName(result.name)

        // Start file watcher if directory handle is available
        const rootHandle = (workspaceManagerRef.current as any).fileSystem?.getRootHandle()
        if (rootHandle) {
          await fileWatcherRef.current.watch(rootHandle)

          // Listen for file changes
          fileWatcherRef.current.onChange((changes) => {
            handleFileChanges(changes)
          })
        }

        toast.success(`Opened workspace: ${result.name}`)
      }
    } catch (error) {
      console.error("Error opening folder:", error)
      if (error instanceof Error) {
        if (error.message.includes("File System Access API is not supported") || 
            error.message.includes("browser doesn't support folder opening")) {
          // Automatically trigger file selection as fallback
          toast.info("Opening file selection dialog (drag & drop also available)", {
            duration: 3000,
          })
          
          // Trigger file input as fallback
          const input = document.createElement('input')
          input.type = 'file'
          input.multiple = true
          input.accept = '*/*'
          input.onchange = (e) => {
            const files = (e.target as HTMLInputElement).files
            if (files && files.length > 0) {
              handleImportFiles(files)
            }
          }
          input.click()
        } else {
          toast.error(`Failed to open folder: ${error.message}`)
        }
      } else {
        toast.error("Failed to open folder")
      }
    }
  }

  // Handle imported files with folder structure support
  const handleImportFiles = async (fileList: FileList) => {
    const fileArray = Array.from(fileList)

    // Check if this is a folder import (files have webkitRelativePath)
    const isFolderImport = fileArray.some(file => (file as any).webkitRelativePath)

    if (isFolderImport) {
      // Build folder structure
      const rootFolder: FileNode = {
        id: `folder-${Date.now()}`,
        name: 'imported',
        type: 'folder',
        children: []
      }

      // Process each file and build the tree
      for (let i = 0; i < fileArray.length; i++) {
        const file = fileArray[i]
        const relativePath = (file as any).webkitRelativePath || file.name
        const pathParts = relativePath.split('/')

        try {
          const content = await file.text()

          // Build nested structure
          let currentNode = rootFolder

          // Navigate/create folders
          for (let j = 0; j < pathParts.length - 1; j++) {
            const folderName = pathParts[j]
            let folder = currentNode.children?.find(
              child => child.name === folderName && child.type === 'folder'
            )

            if (!folder) {
              folder = {
                id: `folder-${Date.now()}-${j}-${folderName}`,
                name: folderName,
                type: 'folder',
                children: []
              }
              currentNode.children = currentNode.children || []
              currentNode.children.push(folder)
            }

            currentNode = folder
          }

          // Add file to final folder
          const fileName = pathParts[pathParts.length - 1]
          const fileNode: FileNode = {
            id: `file-${Date.now()}-${i}-${fileName}`,
            name: fileName,
            type: 'file',
            content,
            language: getLanguageFromFilename(fileName)
          }

          currentNode.children = currentNode.children || []
          currentNode.children.push(fileNode)
        } catch (error) {
          console.error(`Failed to read file ${file.name}:`, error)
          toast.error(`Failed to read ${file.name}`)
        }
      }

      // Get the actual folder name (first part of path)
      const firstFile = fileArray[0]
      const firstPath = (firstFile as any).webkitRelativePath
      if (firstPath) {
        const folderName = firstPath.split('/')[0]
        rootFolder.name = folderName
      }

      // Add the root folder to files
      setFiles(prev => [...prev, rootFolder])
      toast.success(`Imported folder: ${rootFolder.name} (${fileArray.length} files)`)
    } else {
      // Individual file imports (original behavior)
      const importedFiles: FileNode[] = []

      for (let i = 0; i < fileList.length; i++) {
        const file = fileList[i]
        try {
          const text = await file.text()
          importedFiles.push({
            id: `${Date.now()}-${i}-${file.name}`,
            name: file.name,
            type: "file",
            content: text,
            language: getLanguageFromFilename(file.name)
          })
        } catch (error) {
          console.error(`Failed to read file ${file.name}:`, error)
          toast.error(`Failed to read ${file.name}`)
        }
      }

      if (importedFiles.length > 0) {
        setFiles(prev => [...prev, ...importedFiles])
        toast.success(`Imported ${importedFiles.length} file${importedFiles.length > 1 ? 's' : ''}`)
      }
    }
  }

  // Handle external file changes
  const handleFileChanges = (changes: FileChange[]) => {
    changes.forEach(change => {
      if (change.type === "modified") {
        toast.info(`${change.path} was modified externally`)
        // Optionally reload the file
      } else if (change.type === "created") {
        toast.info(`${change.path} was created`)
      } else if (change.type === "deleted") {
        toast.warning(`${change.path} was deleted`)
      }
    })
  }

  // Find and replace handlers
  const handleFind = (query: string, options: FindOptions) => {
    // Monaco editor has built-in find - trigger via keyboard shortcut
    const event = new KeyboardEvent("keydown", {
      key: "f",
      ctrlKey: true,
      bubbles: true,
    })
    document.dispatchEvent(event)
  }

  const handleReplace = (findText: string, replaceText: string, options: FindOptions) => {
    if (!activeFileId) return

    const activeFile = openFiles.find(f => f.id === activeFileId)
    if (!activeFile) return

    // Simple replace
    const regex = options.regex
      ? new RegExp(findText, options.caseSensitive ? "g" : "gi")
      : new RegExp(findText.replace(/[.*+?^${}()|[\]\\]/g, "\\$&"), options.caseSensitive ? "g" : "gi")

    const newContent = activeFile.content.replace(regex, replaceText)
    handleContentChange(activeFileId, newContent)
  }

  const handleReplaceAll = (findText: string, replaceText: string, options: FindOptions) => {
    handleReplace(findText, replaceText, options)
  }

  // File operations
  const handleFileSelect = (fileId: string) => {
    const file = findFileById(files, fileId)
    if (file && file.type === "file") {
      const existingFile = openFiles.find((f) => f.id === fileId)
      if (!existingFile) {
        setOpenFiles([
          ...openFiles,
          {
            id: fileId,
            name: file.name,
            content: file.content || "",
            language: file.language || "plaintext",
            isDirty: false,
          },
        ])
      }
      setActiveFileId(fileId)
    }
  }

  const handleFileClose = (fileId: string) => {
    setOpenFiles(openFiles.filter((f) => f.id !== fileId))
    if (activeFileId === fileId) {
      const remaining = openFiles.filter((f) => f.id !== fileId)
      setActiveFileId(remaining.length > 0 ? remaining[0].id : null)
    }
  }

  const handleContentChange = (fileId: string, content: string) => {
    setOpenFiles(
      openFiles.map((file) =>
        file.id === fileId ? { ...file, content, isDirty: true } : file
      )
    )
  }

  const handleSaveFile = async (fileId: string) => {
    const file = openFiles.find((f) => f.id === fileId)
    if (file) {
      // Save to workspace if available
      try {
        if (workspaceManagerRef.current.isWorkspaceOpen()) {
          await workspaceManagerRef.current.writeFile(fileId, file.content)
          toast.success(`Saved ${file.name}`)
        }

        setOpenFiles(
          openFiles.map((f) => (f.id === fileId ? { ...f, isDirty: false } : f))
        )
      } catch (error) {
        console.error("Error saving file:", error)
        toast.error(`Failed to save ${file.name}`)
      }
    }
  }

  const handleCreateFile = (parentId: string | null, name: string, type: "file" | "folder") => {
    const newNode: FileNode = {
      id: `${Date.now()}-${name}`,
      name,
      type,
      content: type === "file" ? "" : undefined,
      language: type === "file" ? getLanguageFromFilename(name) : undefined,
      children: type === "folder" ? [] : undefined,
    }

    if (parentId === null) {
      setFiles([...files, newNode])
    } else {
      setFiles(addFileToParent(files, parentId, newNode))
    }

    if (type === "file") {
      handleFileSelect(newNode.id)
    }
  }

  const handleDeleteFile = (fileId: string) => {
    setFiles(removeFileById(files, fileId))
    handleFileClose(fileId)
  }

  const handleToggleBreakpoint = (fileId: string, lineNumber: number) => {
    setBreakpoints((prev) => {
      const newMap = new Map(prev)
      const fileBreakpoints = newMap.get(fileId) || new Set()

      if (fileBreakpoints.has(lineNumber)) {
        fileBreakpoints.delete(lineNumber)
      } else {
        fileBreakpoints.add(lineNumber)
      }

      if (fileBreakpoints.size === 0) {
        newMap.delete(fileId)
      } else {
        newMap.set(fileId, fileBreakpoints)
      }

      return newMap
    })
  }

  const handleCommand = async (command: string) => {
    // Expand env vars then parse
    const expanded = parserRef.current.expandVariables(command, envRef.current)
    const parsed = parserRef.current.parse(expanded)
    const validation = validatorRef.current.validate(parsed)

    const termApi = (window as any).__terminal
    const write = (text: string) => termApi?.write?.(text)
    const writeln = (text: string) => termApi?.writeln?.(text)

    if (!validation.valid) {
      validation.errors.forEach(err => writeln?.(`\x1b[31m${err}\x1b[0m`))
      validation.warnings.forEach(warn => writeln?.(`\x1b[33mWarning: ${warn}\x1b[0m`))
      return
    }

    if (!executorRef.current) {
      writeln?.("\x1b[31mTerminal not initialized\x1b[0m")
      return
    }

    const result = await executorRef.current.execute(parsed, validation)
    if (!result.success && result.error) {
      writeln?.(`\x1b[31m${result.error}\x1b[0m`)
    }
  }

  const handleNewFile = () => {
    const fileName = prompt("Enter file name:", "newfile.txt")
    if (fileName) {
      handleCreateFile(null, fileName, "file")
      toast.success(`Created ${fileName}`)
    }
  }

  const handleSaveAll = () => {
    const dirtyFiles = openFiles.filter(f => f.isDirty)
    dirtyFiles.forEach(file => {
      handleSaveFile(file.id)
    })
    if (dirtyFiles.length > 0) {
      toast.success(`Saved ${dirtyFiles.length} files`)
    }
  }

  const handleExport = async () => {
    toast.info("Export functionality coming soon!")
    // Future: Export workspace as ZIP
  }

  const handleFindToggle = () => {
    setShowFindReplace(!showFindReplace)
  }

  const handleReplaceToggle = () => {
    setShowFindReplace(true)
  }

  const handleRunCode = async () => {
    const activeFile = openFiles.find(f => f.id === activeFileId)
    if (!activeFile) {
      toast.error("No file selected")
      return
    }

    // Show execution output panel
    setShowExecutionOutput(true)
    setExecutionStatus('running')
    setExecutionResult(null)

    try {
      const language = detectLanguage(activeFile.name)
      toast.info(`Running ${activeFile.name}...`)

      const result = await executionManagerRef.current.execute(
        activeFile.content,
        language as any,
        { timeout: 30000 }
      )

      setExecutionResult(result)
      setExecutionStatus(result.status)

      if (result.status === 'success') {
        toast.success('Execution complete')
      } else if (result.status === 'error') {
        toast.error('Execution failed')
      } else if (result.status === 'timeout') {
        toast.error('Execution timeout')
      }
    } catch (error) {
      const errorMsg = error instanceof Error ? error.message : String(error)
      setExecutionResult({
        status: 'error',
        stdout: '',
        stderr: errorMsg,
        output: errorMsg,
        exitCode: 1,
        executionTime: 0,
        error: errorMsg,
      })
      setExecutionStatus('error')
      toast.error('Execution error')
    }
  }

  const handleStopExecution = () => {
    executionManagerRef.current.stop()
    setExecutionStatus('idle')
    toast.info('Execution stopped')
  }

  const handleClearOutput = () => {
    setExecutionResult(null)
    setExecutionStatus('idle')
  }

  const handleDebugStart = async () => {
    const activeFile = openFiles.find(f => f.id === activeFileId)
    if (!activeFile) {
      toast.error("No file selected")
      return
    }

    const language = LanguageManager.getLanguageFromFile(activeFile.name)

    try {
      await debugAdapterRef.current.start(language, activeFile.name, activeFile.content || "")

      // Set breakpoints for current file
      const fileBreakpoints = breakpoints.get(activeFileId!)
      if (fileBreakpoints) {
        debugAdapterRef.current.setBreakpoints(
          activeFile.name,
          Array.from(fileBreakpoints)
        )
      }

      setIsDebugging(true)
      setIsDebugPaused(true) // Initially paused or running depending on implementation, but Adapter calls update
      toast.success("Debug session started")
    } catch (error) {
      console.error(error)
      toast.error("Failed to start debugger")
    }
  }

  const handleDebugContinue = async () => {
    await debugAdapterRef.current.continue()
    setIsDebugPaused(false)
  }

  const handleDebugPause = async () => {
    await debugAdapterRef.current.pause()
    setIsDebugPaused(true)
  }

  const handleDebugStepOver = async () => {
    await debugAdapterRef.current.stepOver()
  }

  const handleDebugStepInto = async () => {
    await debugAdapterRef.current.stepInto()
  }

  const handleDebugStepOut = async () => {
    await debugAdapterRef.current.stepOut()
  }

  const handleDebugStop = async () => {
    await debugAdapterRef.current.stop()
    setIsDebugging(false)
    setIsDebugPaused(false)
    toast.info("Debug session stopped")
  }

  const getLanguageFromFilename = (filename: string): string => {
    const ext = filename.split(".").pop()?.toLowerCase()
    const languageMap: Record<string, string> = {
      ts: "typescript",
      tsx: "typescript",
      js: "javascript",
      jsx: "javascript",
      json: "json",
      html: "html",
      css: "css",
      md: "markdown",
      py: "python",
    }
    return languageMap[ext || ""] || "plaintext"
  }

  const findFileById = (nodes: FileNode[], id: string): FileNode | null => {
    for (const node of nodes) {
      if (node.id === id) return node
      if (node.children) {
        const found = findFileById(node.children, id)
        if (found) return found
      }
    }
    return null
  }

  const addFileToParent = (nodes: FileNode[], parentId: string, newNode: FileNode): FileNode[] => {
    return nodes.map((node) => {
      if (node.id === parentId && node.type === "folder") {
        return { ...node, children: [...(node.children || []), newNode] }
      }
      if (node.children) {
        return { ...node, children: addFileToParent(node.children, parentId, newNode) }
      }
      return node
    })
  }

  const removeFileById = (nodes: FileNode[], id: string): FileNode[] => {
    return nodes
      .filter((node) => node.id !== id)
      .map((node) => ({
        ...node,
        children: node.children ? removeFileById(node.children, id) : undefined,
      }))
  }

  useEffect(() => {
    const handleKeyDown = (e: KeyboardEvent) => {
      if ((e.metaKey || e.ctrlKey) && e.key === "s") {
        e.preventDefault()
        if (activeFileId) {
          handleSaveFile(activeFileId)
        }
      }
      if ((e.metaKey || e.ctrlKey) && e.key === "`") {
        e.preventDefault()
        setShowTerminal((prev) => !prev)
      }
      if ((e.metaKey || e.ctrlKey) && e.key === "f") {
        e.preventDefault()
        setShowFindReplace((prev) => !prev)
      }
    }

    window.addEventListener("keydown", handleKeyDown)
    return () => window.removeEventListener("keydown", handleKeyDown)
  }, [activeFileId])

  const activeFile = openFiles.find((f) => f.id === activeFileId)

  const [collabConnected, setCollabConnected] = useState(false);
  const collab = useCollaboration();

  return (
    <div ref={workspaceRef} className="flex h-screen flex-col bg-background">
      {/* Collaboration Connect/Disconnect Button */}
      <div className="flex justify-end p-2">
        <button
          className={`px-3 py-1 rounded font-semibold text-xs ml-2 ${collabConnected ? 'bg-green-200 text-green-900' : 'bg-gray-200 text-gray-700'}`}
          onClick={() => setCollabConnected(c => !c)}
        >
          {collabConnected ? 'Disconnect Collaboration' : 'Connect to Collaboration'}
        </button>
      </div>
      {/* Header */}
      <IDEHeader
        projectId={projectId}
        user={user}
        showTerminal={showTerminal}
        setShowTerminal={setShowTerminal}
        showDebugPanel={showDebugPanel}
        setShowDebugPanel={setShowDebugPanel}
        showCollaborators={showCollaborators}
        setShowCollaborators={setShowCollaborators}
        showAIPanel={showAIPanel}
        setShowAIPanel={setShowAIPanel}
        onRun={handleRunCode}
        onSave={() => activeFileId && handleSaveFile(activeFileId)}
        onSaveAll={handleSaveAll}
        onNewFile={handleNewFile}
        onOpenFolder={handleOpenFolder}
        onExport={handleExport}
        onFind={handleFindToggle}
        onReplace={handleReplaceToggle}
        hasUnsavedChanges={openFiles.some(f => f.isDirty)}
        projectName={workspaceName || "Untitled"}
        fontSize={fontSize}
        onFontSizeChange={setFontSize}
      />
      {/* Main Editor Area */}
      <div className="flex flex-1 overflow-hidden">
        <ResizablePanelGroup direction="horizontal">
          {/* File Explorer */}
          <ResizablePanel defaultSize={20} minSize={15}>
            <FileExplorer
              files={files}
              activeFileId={activeFileId}
              onFileSelect={handleFileSelect}
              onCreateFile={handleCreateFile}
              onDeleteFile={handleDeleteFile}
              onOpenFolder={handleOpenFolder}
              onImportFiles={handleImportFiles}
            />
          </ResizablePanel>
          <ResizableHandle />
          {/* Editor + Terminal */}
          <ResizablePanel defaultSize={60}>
            <ResizablePanelGroup direction="vertical">
              {/* Editor */}
              <ResizablePanel defaultSize={showTerminal ? 70 : 100}>
                <div className="flex h-full flex-col">
                  {/* Breadcrumb */}
                  {activeFile && (
                    <Breadcrumb
                      path={activeFile.name}
                      onNavigate={(path) => console.log("Navigate to:", path)}
                    />
                  )}
                  {/* Find & Replace */}
                  {showFindReplace && (
                    <FindReplace
                      onFind={handleFind}
                      onReplace={handleReplace}
                      onReplaceAll={handleReplaceAll}
                      onClose={() => setShowFindReplace(false)}
                    />
                  )}
                  {/* Split Editor */}
                  <div className="flex-1">
                    <SplitEditor
                      openFiles={openFiles}
                      activeFileId={activeFileId}
                      onFileSelect={handleFileSelect}
                      onFileClose={handleFileClose}
                      onContentChange={handleContentChange}
                      onSave={handleSaveFile}
                      breakpoints={breakpoints}
                      onToggleBreakpoint={handleToggleBreakpoint}
                      projectId={projectId}
                      user={user}
                      fontSize={fontSize}
                      debugLine={activeLine}
                    />
                  </div>
                </div>
              </ResizablePanel>
              {/* Terminal */}
              {showTerminal && (
                <>
                  <ResizableHandle />
                  <ResizablePanel defaultSize={30} minSize={20}>
                    <Terminal
                      onCommand={handleCommand}
                      onClose={() => setShowTerminal(false)}
                    />
                  </ResizablePanel>
                </>
              )}
            </ResizablePanelGroup>
          </ResizablePanel>
          <ResizableHandle />
          {/* Right Sidebar */}
          <ResizablePanel defaultSize={20} minSize={15}>
            <ResizablePanelGroup direction="vertical">
              {showDebugPanel && (
                <>
                  <ResizablePanel defaultSize={showCollaborators || showExecutionOutput ? 33 : 50}>
                    <AdvancedDebugPanel
                      isDebugging={isDebugging}
                      isPaused={isDebugPaused}
                      breakpoints={breakpoints}
                      stackFrames={debugAdapterRef.current.getStackTrace()}
                      onStart={handleDebugStart}
                      onPause={handleDebugPause}
                      onContinue={handleDebugContinue}
                      onStepOver={handleDebugStepOver}
                      onStepInto={handleDebugStepInto}
                      onStepOut={handleDebugStepOut}
                      onStop={handleDebugStop}
                    />
                  </ResizablePanel>
                  <ResizableHandle />
                </>
              )}
              {showCollaborators && collabConnected && collab ? (
                <>
                  <ResizablePanel defaultSize={showDebugPanel || showExecutionOutput ? 33 : 50}>
                    <EnhancedCollaboratorsPanel
                      user={user}
                      activeUsers={collab.users.map(u => ({
                        userId: u.id?.toString() || '',
                        userName: u.name || u.userName || u.email || 'User',
                        userEmail: u.email || '',
                        color: u.color || '#888',
                        status: 'online',
                        activity: 'editing',
                        isTyping: false,
                        currentFile: '',
                        lastSeen: new Date(),
                      }))}
                      messages={collab.chatMessages}
                      onSendMessage={msg => Promise.resolve(collab.sendMessage(msg, { id: user.id, name: user.user_metadata?.name || user.email }))}
                      onRoomSettingsChange={onRoomSettingsChange}
                    />
                  </ResizablePanel>
                  <ResizableHandle />
                </>
              ) : showCollaborators ? (
                <>
                  <ResizablePanel defaultSize={33}>
                    <div className="flex flex-col h-full p-4">
                      <div className="flex items-center justify-center h-full text-xs text-muted-foreground">
                        Collaboration is disconnected.
                      </div>
                      {/* Room settings always available */}
                      <div className="mt-auto border-t border-border pt-4">
                        <div className="space-y-2">
                          <div className="flex items-center gap-2">
                            <span className="text-xs font-medium">Room Settings:</span>
                          </div>
                          <div className="flex items-center gap-2">
                            <span className="text-xs">Room ID:</span>
                            <div className="h-7 flex items-center px-2 text-xs bg-muted rounded w-32">
                              {user.roomId || 'default-room'}
                            </div>
                          </div>
                          <div className="flex items-center gap-2">
                            <span className="text-xs">Password:</span>
                            <div className="h-7 flex items-center px-2 text-xs bg-muted rounded w-24">
                              {user.roomPassword ? '•••••' : 'none'}
                            </div>
                          </div>
                          <div className="flex gap-1">
                            <Button
                              variant="ghost"
                              size="sm"
                              className="h-7 text-xs"
                              onClick={() => {
                                console.log('IDE workspace New Room button clicked');
                                try {
                                  const chars = 'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789';
                                  let roomId = '';
                                  for (let i = 0; i < 8; i++) {
                                    roomId += chars.charAt(Math.floor(Math.random() * chars.length));
                                  }
                                  const chars2 = 'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789!@#$%^&*';
                                  let password = '';
                                  for (let i = 0; i < 12; i++) {
                                    password += chars2.charAt(Math.floor(Math.random() * chars2.length));
                                  }
                                  console.log('IDE Generated room:', roomId, password);
                                  if (onRoomSettingsChange) {
                                    onRoomSettingsChange({ roomId, roomPassword: password });
                                  }
                                  toast.success('New room created! Room ID: ' + roomId);
                                } catch (error) {
                                  console.error('IDE Error creating new room:', error);
                                  toast.error('Failed to create new room');
                                }
                              }}
                              title="Create new room ID and password"
                            >
                              <RefreshCw className="h-3 w-3 mr-1" />
                              New Room
                            </Button>
                            <Button
                              variant="ghost"
                              size="sm"
                              className="h-7 text-xs"
                              onClick={() => {
                                console.log('IDE workspace New Password button clicked');
                                try {
                                  const chars = 'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789!@#$%^&*';
                                  let password = '';
                                  for (let i = 0; i < 12; i++) {
                                    password += chars.charAt(Math.floor(Math.random() * chars.length));
                                  }
                                  console.log('IDE Generated password:', password);
                                  if (onRoomSettingsChange) {
                                    onRoomSettingsChange({ roomId: user.roomId || 'default-room', roomPassword: password });
                                  }
                                  toast.success('New password generated!');
                                } catch (error) {
                                  console.error('IDE Error generating new password:', error);
                                  toast.error('Failed to generate new password');
                                }
                              }}
                              title="Generate new password"
                            >
                              <Key className="h-3 w-3 mr-1" />
                              New Password
                            </Button>
                          </div>
                        </div>
                      </div>
                    </div>
                  </ResizablePanel>
                  <ResizableHandle />
                </>
              ) : null}
              {showExecutionOutput && (
                <ResizablePanel defaultSize={showDebugPanel || showCollaborators ? 34 : 100}>
                  <ExecutionOutput
                    result={executionResult}
                    status={executionStatus}
                    language={activeFile?.language}
                    onClear={handleClearOutput}
                    onStop={executionStatus === 'running' ? handleStopExecution : undefined}
                  />
                </ResizablePanel>
              )}
              {showAIPanel && (
                 <>
                  <ResizablePanel defaultSize={30}>
                    <AIAssistantPanel
                      activeFileContent={activeFile?.content}
                      activeFileName={activeFile?.name}
                      onClose={() => setShowAIPanel(false)}
                    />
                  </ResizablePanel>
                  <ResizableHandle />
                 </>
              )}
            </ResizablePanelGroup>
          </ResizablePanel>
        </ResizablePanelGroup>
      </div>
      {/* Drag & Drop Styles */}
      <style jsx global>{`
        .drag-over {
          outline: 2px dashed hsl(var(--accent));
          outline-offset: -2px;
          background-color: hsl(var(--accent) / 0.1);
        }
      `}</style>
    </div>
  )
}
